.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  --base-primary-light: #BBCAEF; 
  --base-primary-dark: #5878CB; 
  --base-secondary-light: #F8D8CE; 
  --base-secondary-dark: #E88D70;  
  --base-solid-light: #F4F4F4; 
  --base-solid-medium: #d9d9d9; 
  --base-solid-dark: #878787; 
  --base-primary-glow: #BBCAEFc0; 
  --base-secondary-glow: #F8D8CEb0;
  --base-solid-glow: #c0c4d2c0; 
  --base-like-light: #efbbcf; 
  --base-like-glow: #efbbcfc0; 
  --base-like-dark: #e27ea5; 
  
}

.bg-base{
  background-color: var(--base-primary-light); 
}

div.shout-list-content-container{
  min-height: 100vh; 
  border-radius: 1rem; 
}

button{
  border: none; 
  border-radius: 1.8rem; 
  padding-right: 1.8rem; 
  padding-left: 1.8rem; 
  padding-top: .5rem; 
  padding-bottom: .5rem; 
  margin-right: 1rem; 
}

button.boost-button{
  box-shadow: 0px .8rem 2.5rem .1rem var(--base-primary-glow);
  background-color: var(--base-primary-light); 
  color: var(--base-primary-dark); 
  display: block; 
}

button.like-button{
  box-shadow: 0px .8rem 2.5rem .1rem var(--base-like-glow);
  background-color: var(--base-like-light); 
  color: var(--base-like-dark); 
  display: block; 
}
button.unlike-button{
  background-color: white; 
  color: var(--base-like-dark); 
  box-shadow: 0px .8rem 2.5rem .1rem var(--base-like-glow);
  display: block; 
  border: .2rem solid var(--base-like-light); ; 
}
button.unlike-button:hover{
  background-color: var(--base-like-light);
}

button.reply-button{
  box-shadow: 0px .8rem 2.5rem .1rem var(--base-secondary-glow);
  background-color: var(--base-secondary-light); 
  color: var(--base-secondary-dark); 
  display: block; 
}

button.like-button:hover,button.reply-button.hover,button.boost-button.hover{
  cursor: pointer; 
  color: white; 
}